import React, { useEffect, useState } from 'react';
import classes from './Chats.module.css'
import { useCookies } from 'react-cookie';

export default function Chats({ data }) {
  const { id, content, time, ip } = data;
  const [cookies] = useCookies()
  const [check,setCheck] = useState(false);   //  chat    state

  useEffect(() => {
    const userIp = cookies.ip[2] + '.' + cookies.ip[3]
    if(userIp === ip && cookies.id.id === id) setCheck(true);   //  ip id 
  },[data])


  const today = new Date();
  const now = today.getTime();    //     
  const timestamp = (now - time) / 1000;    //         
  let when;   //    

  switch (true) {
    case (timestamp < 60):   //  1     1 
      when = '1';
      break;
    case (timestamp < 3600): //  1        
      when = Math.floor(timestamp / 60) + ''
      break;
    case (timestamp < 86400):  //          
      when = Math.floor(timestamp / 3600) + ''
      break;
    default:    //       
      const date = new Date(time);
      const month = date.getMonth() + 1;
      const day = date.getDate();

      when = month + "/" + day;
  }


  return (
    <>
      {check ?
        <div className={classes.preview_my}>
          <div>
            <div className={classes.pic}></div>
          </div>
          <div className={classes.bubble_wrap}>
            <h4>{id}<span>({ip})</span></h4>
            <div className={classes.content}>
              <p>{content}</p>
              <span>{when}</span>
            </div>
          </div>
        </div>
          :
        <div className={classes.preview}>
          <div>
            <div className={classes.pic}></div>
          </div>
          <div className={classes.bubble_wrap}>
            <h4>{id}<span>({ip})</span></h4>
            <div className={classes.content}>
              <p>{content}</p>
              <span>{when}</span>
            </div>
          </div>
        </div>
      }
    </>

  )
}
