import React, { useEffect, useRef, useState } from 'react';
import { useCookies } from 'react-cookie';
import Chatting from '../components/Chatting';
import classes from './Chat.module.css'
import { useMutation, useQuery, useQueryClient } from 'react-query';
import getPostList, { getPostListLeng } from '../api/getPost';
import Logout from '../components/Logout';
import Loding from '../components/Loding';
import { useNavigate } from 'react-router-dom';
import { getDatabase, onChildAdded, ref } from 'firebase/database'
import { app } from '../Firebase/FirebaseClient';
import loadIp from '../api/loadIp';


export default function Chat() {
  const [cookies, setCookie, removeCookie] = useCookies(['id']);
  const navigation = useNavigate();
  const queryClient = useQueryClient();

  const [userId, setUserId] = useState();     //    state
  const [loding, setLoding] = useState(true);   //   state
  const [sending, setSending] = useState(true);   //    state

  const [page, setPage] = useState(20);   //     state
  const newRef = useRef(0);   //     ref
  const pageRef = useRef(0);  //  db   state

  const formRef = useRef();
  const lengRef = useRef();   //  db   ref
  const ipRef = useRef();     //  ip   ref
  const date = new Date();
  const time = date.getTime();  //        
  const endRef = useRef();    // html  DOM   ref

  /*         refetching */
  useEffect(() => {
    pageRef.current = page + newRef.current;
    queryClient.invalidateQueries(['content']);
  }, [page])

  const { data, isLoading } = useQuery(['content'], async () => {   //  db 
    const result = await getPostList(pageRef.current)
    return result
  })    //   promise     

  /*    mutation */
  const updateMutaition = useMutation((chat) => {
    fetch('https://test2-23ab7-default-rtdb.asia-southeast1.firebasedatabase.app/chattingList.json', {
      method: "POST",
      body: JSON.stringify({
        id: chat.id,
        content: chat.content,
        time,
        ip: chat.ip[2] + '.' + chat.ip[3]
      })
    })
      .then((res) => {
        res.json();
        newRef.current++;
        queryClient.invalidateQueries(['content']);   //   refetching
        setTimeout(() => {    //      
          endRef.current.scrollIntoView({ behavior: 'smooth' });
        }, 130)
      })
  })

  useEffect(() => {
    if (cookies.id) {
      setTimeout(() => {
        setLoding(false);   //  
      }, 1200)

      loadIp()    //  ip  
      .then((res) => {
        ipRef.current = res.split('.');
        setCookie('ip',ipRef.current)   //   
      })

      setUserId(cookies.id.id)
      getPostListLeng() // db   
        .then((res) => lengRef.current = res.length)
    }
    else {    //     
      alert('  ')
      navigation('/')
    }
  }, [])

  

  /*    */
  const cntRef = useRef(0);
  const [sendingMsg, setSendingMsg] = useState('');

  /* db   refetching */
  const db = getDatabase(app);
  const commentsRef = ref(db, 'chattingList');

  onChildAdded(commentsRef, () => {
    queryClient.invalidateQueries(['content']);
  });

  /*    */
  async function chatBtn(e) {
    e.preventDefault();

    if (sending) {
      setSending(false);  //    

      const interval = setInterval(() => {    //    interval
        if (cntRef.current < 4) {
          setSendingMsg(word => word + '.');
          cntRef.current++
        }
        else {
          cntRef.current = 0;
          setSendingMsg('')
        }
      }, 200)

      const chat = {
        id: userId,
        content: formRef.current.text.value,
        ip:ipRef.current
      }
      updateMutaition.mutate(chat);
      formRef.current.text.value = ''   // form  

      clearInterval(interval)   // interval 
      setSending(true);   //  
      setSendingMsg('');    //  
    }
  }

  function keydown(e) {   // enter  
    if (e.key === 'Enter' && sending) chatBtn(e)
  }

  if (cookies.id) {   //          
    return (
      <>
        {loding && <Loding />}
        <div className={classes.chat}>
          {isLoading ? 
          null 
          : 
          <Chatting data={data} setPage={setPage} page={page} leng={lengRef.current}/>}
          <div className={classes.end} ref={endRef}></div>
          <div className={classes.logout}>
            <Logout username={userId} removeCookie={removeCookie} />
          </div>
          <form ref={formRef} onSubmit={chatBtn} onKeyDown={keydown}>
            <div className={classes.chat_form}>
              <textarea name='text'></textarea>
            </div>
            <div className={classes.util}>
              {sending ? 
              <div className={classes.submit}>
                <input type="submit" value="" />
              </div> 
              :
              <p className={classes.sending}>{sendingMsg}</p>
              }
            </div>
          </form>
        </div>
      </>
    )
  }
}
